	module.exports = {"data":{"markdownRemark":{"html":"<p>Lisons <strong>1 Corinthiens 14:2‭-‬3‭, ‬27-28 :</strong></p>\n<blockquote>\n<p>« En effet, celui qui parle en langue <strong>ne parle pas aux hommes, mais à Dieu</strong>, car personne ne le comprend, et c’est en esprit qu’il dit des mystères. Celui qui prophétise, au contraire, parle aux hommes, les édifie, les exhorte, les console. En est-il qui parlent en langue, que deux ou trois au plus parlent, chacun à son tour, et que quelqu'un interprète; s’il n’y a point d’interprète, qu’on se taise dans l’Église, <strong>et qu’on parle à soi-même et à Dieu.</strong> »</p>\n</blockquote>\n<p>Les versets à l'origine de cette question se trouvent dans <strong>1 Corinthiens 14:18-19 :</strong></p>\n<blockquote>\n<p>« Je rends grâces à Dieu de ce que je parle en langue plus que vous tous; mais, dans l'Eglise, j'aime mieux dire cinq paroles avec mon intelligence, afin d'instruire aussi les autres, que dix mille paroles en langue. »</p>\n</blockquote>\n<p>Pour mieux comprendre ce que Paul dit ici, remontons quelques versets plus haut et lisons <strong>1 Corinthiens 14:6-11 :</strong></p>\n<blockquote>\n<p>« Et maintenant, frères et soeurs, en quoi vous serais-je utile si je venais chez vous en parlant en langues au lieu de vous apporter une parole de révélation, de connaissance, de prophétie ou d'enseignement?\nSi les objets inanimés qui rendent un son, comme une flûte ou une harpe, ne rendent pas des sons distincts, comment reconnaîtra-t-on la mélodie jouée sur eux? Et si la trompette rend un son confus, qui se préparera au combat? Il en va de même pour vous: si votre langue ne donne pas une parole intelligible, comment saura-t-on ce que vous dites? En effet, vous parlerez en l'air. <strong>Si nombreuses que puissent être les diverses langues dans le monde, aucune [d'entre elles] n'est dépourvue de signification.</strong> Si donc je ne connais pas le sens d'une langue, je serai un étranger pour celui qui parle, et celui qui parle sera un étranger pour moi. »</p>\n</blockquote>\n<p>Nous pouvons voir ici que Paul parle bien dans ce chapitre de l'utilisation de langues diverses compréhensibles qui existaient à travers le monde à cette époque !</p>\n<p>On comprends d'ailleurs la raison pour laquelle il dit parler en langue plus que tous en regardant de plus près son parcours :</p>\n<ol>\n<li>Paul écrivit ses épîtres aux différentes églises en <strong>grec</strong>. Il fit d'ailleurs un discourut à l'Aréopage, à Athènes, capitale de la Grèce. (<strong>Actes 17:16-31</strong>)</li>\n<li>Paul était un juif très instruit et un érudit qui parlait la <strong>langue hébraïque</strong> couramment. Certaines de ces lettres bien qu'écrites en grec étaient parfois difficiles à comprendre pour les moins instruits (<strong>Actes 22:2,3, 2 Pierre 3:15,16</strong>)</li>\n<li>Paul parlait très certainement <strong>le latin</strong> également car il était citoyen romain de naissance. Il déclare d'ailleurs dans son épître aux romains son désir de visiter leur église (<strong>Actes 22:25-29 ; Romain 1:13-15</strong>)</li>\n<li>Paul, était un grand missionnaire dédié principalement à évangéliser les \"étrangers\", \"païens\" ou \"barbares\" (<strong>Romains 1:14, Romains 11:13</strong>). Il voyageât dans beaucoup de pays à travers l'Asie et l'Europe visitant environs 48 villes que l'on retrouve dans ses épître ainsi que dans le livre des Actes des apôtres. Au cours de tous ces voyages missionnaires il dût très certainement donc parler aussi plusieurs autres langues sous l'influence du Saint-Esprit, afin que ces auditeurs comprennent le message qu'il leur transmettait, tout comme les disciples le jour de la pentecôte.</li>\n</ol>","frontmatter":{"title":"Pourquoi Paul disait-il parler en langues plus que tous ?"}}},"pathContext":{"slug":"/pourquoi-paul-disait-t-il-parler-en-langue-plus-que-tous/"}}

